.App-logo {
  height: 40vmin;
}


.App-link {
  color: #09d3ac;
}

div.Section{
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}
div.column {
  padding-left: 10px;
  padding-right: 10px;
}
div.column-left {
  flex: 35% 1;
}

div.column-blog-left {

  flex: 55% 1;
}

div.column-blog-right {
  flex: 45% 1;
  background: url("https://images.unsplash.com/photo-1531903941236-b67dd2867cff?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=946&q=80");
  background-size: cover;
  width: 100%;
  height: 100vh;
}

a.blog-list-title {
  font-weight: bold;
  text-decoration: none;
  color: black;
}

div.column-left h1 {
  font-size: calc(40px + 2vmin);
  font-weight: bold;
}

div.column-right {
  flex: 65% 1;
  background: url("https://images.unsplash.com/photo-1534125956906-1941fd729155?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1100&q=80");
  background-size: cover;
  width: 100%;
  height: 100vh;
}

div.Section:nth-of-type(2) div.column-left {
  flex: 55% 1;
  background: url("https://images.unsplash.com/photo-1549298620-833e1e37670b?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=800&q=60");
  background-size: cover;
  width: 100%;
  height: 100vh;
}

div.Section:nth-of-type(2) div.column-right {
  flex: 45% 1;
  background: none;
  width: auto;
  height: auto;

}

div.Section:nth-of-type(3) div.column-right {
  flex: 45% 1;

}


div.form-column-left {
  flex: 35% 1;
}
div.form-column-right{
  flex: 35% 1;
}


.form {
  padding-right:40px;
  padding-left: 10px;
}

.form-item{
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: solid rgba(207, 237, 200, 1);
  display: block;
  width: 100%;
  background: 0 0;
  font-family: ff-more-web-pro;
  font-size: 22px;
  color: #555;
  line-height: 1.2;
  padding: 0 2px;

}

.form-item-title {
  font-weight: bold;
  color: #333;
  line-height: 1.2;
  display: block;
  text-align: left;
}

.form-btn {
  font-family: ff-more-web-pro, serif;
  font-size: 15px;
  display: flex;
  border: none;
  border-bottom: solid rgba(207,237,200,1);
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
}

footer {
  text-align: center;
}

.posts-listing {
  margin-left: 9px;
}

.highlight-container, .highlight {
  position: relative;
}

.highlight-container {
  display: inline-block;
}
.highlight-container:before, .highlight-container:after {
  content: " ";
  display: block;
  height: 90%;
  width: 100%;
  margin-left: -3px;
  margin-right: -3px;
  position: absolute;

}

.highlight-container:before {
  background: rgba(207, 237, 200, 1);
  -webkit-transform: rotate(2deg);
          transform: rotate(2deg);
  top: -1px;
  left: -1px;
}
.highlight-container:after {
  background: rgba(207, 237, 200, 0.6);
  top: 3px;
  right: -2px;
}
.highlight-container .highlight {
  color: #333;
  z-index: 4;
}






.highlight-container-blog, .highlight {
  position: relative;
}


.highlight-container-blog {
  display: inline-block;
}
.highlight-container-blog:before, .highlight-container-blog:after {
  content: " ";
  display: block;
  height: 90%;
  width: 100%;
  margin-left: -3px;
  margin-right: -3px;
  position: absolute;

}

.highlight-container-blog:before {
  background: rgba(255, 217, 235, 1);
  -webkit-transform: rotate(2deg);
          transform: rotate(2deg);
  top: -1px;
  left: -1px;
}
.highlight-container-blog:after {
  background: rgba(255, 217, 235, 0.6);
  top: 3px;
  right: -2px;
}
.highlight-container-blog .highlight {
  color: #333;
  z-index: 4;
}
